import { Card, CardHeader, CardMedia, Skeleton, SvgIcon } from '@mui/material';
import classNames from 'classnames';
import styles from './post-card.module.css';
import { isLiked } from '../../utils/posts';
import { Link } from 'react-router-dom';
import { PostsContext } from '../../contexts/posts-context';
import { UserContext } from '../../contexts/current-user-context';
import { useContext } from 'react';
import GradeOutlinedIcon from '@mui/icons-material/GradeOutlined';
import { movieCountry, movieYear, movieRatingKP, movieDirector } from '../../utils/movie';

export function PostCard({ _id, title, isPublished, author, image, created_at, likes, text }) {
    const { isLoading, onPostLike, onPostDelete } = useContext(PostsContext);
    const { currentUser } = useContext(UserContext);

    const like = isLiked(likes, currentUser?._id);

    const titleWithYear = `${title} (${movieYear(text)})`;

    function FireIcon(props) {
        return (
            <SvgIcon {...props}>
                <path
                    d="M12 23C16.1421 23 19.5 19.6421 19.5 15.5C19.5 14.6345 19.2697 13.8032 19 13.0296C17.3333 14.6765 16.0667 15.5 15.2 15.5C19.1954 8.5 17 5.5 11 1.5C11.5 6.49951 8.20403 8.77375 6.86179 10.0366C5.40786 11.4045 4.5 13.3462 4.5 15.5C4.5 19.6421 7.85786 23 12 23ZM12.7094 5.23498C15.9511 7.98528 15.9666 10.1223 13.463 14.5086C12.702 15.8419 13.6648 17.5 15.2 17.5C15.8884 17.5 16.5841 17.2992 17.3189 16.9051C16.6979 19.262 14.5519 21 12 21C8.96243 21 6.5 18.5376 6.5 15.5C6.5 13.9608 7.13279 12.5276 8.23225 11.4932C8.35826 11.3747 8.99749 10.8081 9.02477 10.7836C9.44862 10.4021 9.7978 10.0663 10.1429 9.69677C11.3733 8.37932 12.2571 6.91631 12.7094 5.23498Z"
                    fill="#000"
                ></path>
            </SvgIcon>
        );
    }

    function CloseIcon(props) {
        return (
            <SvgIcon {...props}>
                <path d="M12.0007 10.5865L16.9504 5.63672L18.3646 7.05093L13.4149 12.0007L18.3646 16.9504L16.9504 18.3646L12.0007 13.4149L7.05093 18.3646L5.63672 16.9504L10.5865 12.0007L5.63672 7.05093L7.05093 5.63672L12.0007 10.5865Z"></path>
            </SvgIcon>
        );
    }

    function handleClickLike() {
        onPostLike({ likes, _id });
    }

    function handleClickRemove() {
        if (author._id === currentUser._id) {
            onPostDelete({ _id });
        }
    }

    return (
        <Card sx={{ maxWidth: 400 }} className={classNames(styles.item)} data-id={_id}>
            <div className={classNames(styles.wrapper, `${!isPublished && styles.unpublished}`)}>
                <CloseIcon className={classNames(styles.remove_icon)} onClick={handleClickRemove} />
                {isLoading ? (
                    <>
                        <Skeleton
                            width={310}
                            height={265}
                            animation="wave"
                            variant="rectangular"
                            style={{ marginBottom: 6 }}
                        />
                        <Skeleton
                            animation="wave"
                            height={40}
                            width="100%"
                            style={{ marginBottom: 24 }}
                        />
                        <Skeleton animation="wave" height={20} width="40%" />
                        <Skeleton
                            animation="wave"
                            height={20}
                            width="60%"
                            style={{ marginBottom: 20 }}
                        />
                        <Skeleton animation="wave" height={20} width="100%" />
                    </>
                ) : (
                    <>
                        <Link to={`/post/${_id}`} className="post__link">
                            <CardMedia
                                component="img"
                                image={image ? image : './images/post-default.svg'}
                                alt=""
                                className={classNames(styles.media)}
                            />
                        </Link>
                        <Link to={`/post/${_id}`} className={styles.link}>
                            <CardHeader className={styles.title} title={titleWithYear}></CardHeader>
                        </Link>

                        <div className={classNames(styles.body)}>
                            <div className={styles.meta}>
                                <div className={classNames(styles.country)}>
                                    Страна: {movieCountry(text)}
                                </div>
                                <div className={classNames(styles.director)}>
                                    Режиссер: {movieDirector(text)}
                                </div>
                            </div>
                            <div className={styles.footer}>
                                <div className={styles.rating}>
                                    <div className={classNames(styles.rating_kp)}>
                                        <GradeOutlinedIcon /> {movieRatingKP(text)}
                                    </div>
                                </div>
                                <div
                                    data-like={like}
                                    className={classNames(styles.like, {
                                        [styles.like__active]: like,
                                    })}
                                    style={{ display: 'flex', alignItems: 'center' }}
                                    onClick={handleClickLike}
                                >
                                    <FireIcon /> {likes?.length}
                                </div>
                            </div>
                        </div>
                    </>
                )}
            </div>
        </Card>
    );
}
